
import { defineComponent, SetupContext } from 'vue'
export default defineComponent({
  props: {
    status: Number
  },
  setup(props: any, context: SetupContext) {
    const getStatusText = (status: number) => {
      const PRESCRIPT_STATUS: any = {
        1: "問診",
        2: "医師選択中",
        3: "診察中",
        4: "購入中",
        0: "処方手続き完了",
      }
      return PRESCRIPT_STATUS[status]
    }
    return {
      getStatusText
    }
  }
})
